.wrapper {
  display: flex;
  flex-direction: column;
  width: 880px;
  align-items: flex-start;
  justify-content: center;
  padding: 0 1rem;

  .cartList {
    list-style: none;
    padding: 0;

    .item {
      display: grid;
      grid-template-columns: 180px 500px 180px;
      margin-bottom: 20px;
      max-height: 100px;

      &__image {
        display: flex;
        justify-content: center;
        align-items: center;
        max-height: 100px;
        width: 100%;
        img {
          align-self: center;
          max-height: 100%;
          max-width: 100%;
        }
      }

      &__details {
        display: flex;
        align-items: center;
        margin-left: 1rem;

        &__title {
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: normal;
        }
      }

      &__controls {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        width: 180px;

        &__price {
          margin-right: 10px;
        }
        &__discount {
          margin-right: 10px;
          text-decoration: line-through;
        }

        &__input {
          max-width: 40px;
        }

        &__button {
          margin-left: 5px;
        }
      }
    }
  }
}
